









































import { Component, Vue } from 'vue-property-decorator';

@Component({
  components: {},
})
export default class Header extends Vue {
  hover = {
    menu: true,
    auther: false,
    other: false,
    chat: false,
  };

  Next(name: string) {
    if (this.$route.name === name) return;
    this.$router.push({ name });
  }
}
